<template>
<div>
    <p class="sub-title-step">{{store.state.shareKey.lock_list_ids.length}} keys selected. You can also <span @click="setOrUnset(true)" class="sub-title-actions">select all</span> or <span @click="setOrUnset(false)" class="sub-title-actions">clear all</span></p>
<div class="users">
     <div v-if="group.doorOwned.length > 0" class="user-container" :class="{'open-container':group.showDetail }" :key="group.description" v-for="(group, i) in groups">
    <div><span class="name-container">  <img class="avatar-activity"  src="@/assets/img/group.png"><ul class="group-list"><li>{{group.description}}</li>
    <li class="sub-title-rules">{{group.locks.length}} keys</li></ul>
        <img :disabled="group.doorOwned.length === 0" @click="setActive(group, i)"  :class="{'active-arrow': group.showDetail}"  class="selected-img" src="@/assets/havr-icon/right-arrow.svg">
    </span>
     </div>
      <div @click="chooseKey(lock, i, index)"  class="lock-container" v-if="group.showDetail" v-for="(lock, index) in group.doorOwned" :key="lock.id">
         <p  class="lock-label">{{lock.description}} <img v-if="lock.selected"  class="selected-img" src="@/assets/img/outline.png"></p>
          </div>
              <hr class="group-separator">
          </div>

 </div>
</div>

</template>
<script>
import store from "@/flulux/store";
import repository from "@/repository/keychain";
import lodash from "lodash";
export default {
  data() {
    return {
      store: store,
      groups: [],
      selectedKeys: []
    };
  },
  async mounted() {
    if (this.store.state.shareKey.shallowCopy.length > 0) {
      return (this.groups = this.store.state.shareKey.shallowCopy);
    }
    const data = await this.getDoorlocksWithGroups();
  },
  methods: {
    async getDoorlocksWithGroups() {
      let res = await repository.getLockGroupsWithDoorlocks();
      let groups = res.lock_groups;
      let locks = res.door_locks;
      locks.map(i => {
        i.selected = false;
      });
      groups.map(i => {
        i.doorOwned = [];
        i.showDetail = false;
        i.locks.map(lock => {
          i.doorOwned.push(lodash.filter(locks, { id: lock })[0]);
        });
      });
      this.groups = groups;
    },
    setActive(group, i) {
      group.showDetail = !group.showDetail;
      this.$set(this.groups, i, group);
    },
    chooseKey(lock, i, index) {
      if (lock.selected) lock.selected = false;
      else lock.selected = true;
      this.$set(this.groups[i].doorOwned[index], i, lock);
      this.createObjectForStore();
    },
    createObjectForStore() {
      this.selectedKeys = [];
      const keysToShare = lodash.filter(this.groups, {
        doorOwned: [{ selected: true }]
      });
      keysToShare.forEach(i => {
        i.doorOwned.forEach(lock => {
          if (lock.selected) {
            this.selectedKeys.push({
              door_lock_id: lock.id,
              lock_group_id: i.id
            });
          }
        });
      });
      this.store.actions.SET_SHARED_KEYS_TO_SHARE(this.selectedKeys);
      this.store.state.shareKey.shallowCopy = this.groups;
    },
    setOrUnset(p) {
      this.groups.forEach((i, index) => {
        i.doorOwned.forEach(lock => {
          if (lock) {
            lock.selected = p;
          }
        });
      });
      this.$set(this.groups);
      this.createObjectForStore();
    }
  }
};
</script>

